.record {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 3rem;
  margin-top: 1rem;
  padding: 1.2rem 0;
  // padding: 120px 0;

  transition: $transition-base;

  @media (min-width: $sm) {
    margin-bottom: 2rem;
    margin-top: 0;
  }

  &::before {
    content: '';
    position: absolute;
    top: -0.5rem;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $primary-color;

    @media (min-width: $sm) {
      top: 0;
    }
  }

  &::after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $primary-color;

    @media (min-width: $sm) {
      bottom: 0;
    }
  }

  @media (min-width: $xs) {
    margin-top: 1rem;
    padding: 1rem 0;
  }

  @media (min-width: $sm) {
    margin-top: 0.8rem;
    margin-bottom: 1.9rem;
    padding-bottom: 1.2rem;
    padding-top: 1.5rem;
    padding-left: 0.5rem;
  }

  @media (min-width: $md) {
    // border-right-color: transparent;
    // border-left-color: transparent;
    margin-top: 0.4rem;
    margin-bottom: 1.5rem;
    padding: 1.8rem 0 1.3rem 0.6rem;
  }

  @media (min-width: $xl) {
    margin-top: 1rem;
  }

  &:focus,
  &:hover {
    outline: none;
    border-radius: 0;
    transition: $transition-base;
    cursor: pointer;
    & .record__title span {
      opacity: 1;
    }

    & .link-text--more svg {
      width: 40px;
      transform: translateX(6px);
      transition: $transition-base;
    }
  }

  & .icon {
    transition: $transition-base;
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.2rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    margin: 0;
    padding-bottom: 1.7rem;

    @media (min-width: $sm) {
      padding-bottom: 0;
    }

    color: $primary-color;

    @include ff(
      'font-size',
      (
        375px: 27px,
        768px: 60px,
      )
    );

    & span {
      opacity: 0.6;
    }
  }

  & .link-text {
    margin-left: auto;
    @media (min-width: $sm) {
      margin-bottom: 17px;
    }
    @media (min-width: $md) {
      margin-bottom: 0;
    }
  }

  &--club {
    padding: 1rem 0;
    & .record__title {
      padding-bottom: 0.5rem;
      font-size: 26px;
      @include ff(
        'font-size',
        (
          420px: 26px,
          768px: 50px,
          1024px: 30px,
          1440px: 50px,
        )
      );

      @media (min-width: $sm) {
        padding-bottom: 0;
        font-size: 50px;
      }

      @media (min-width: $xl) {
        font-size: 36px;
      }
      @media (min-width: $xxl) {
        font-size: 50px;
      }
    }

    & .record__subtitle {
      margin: 0;
      margin-bottom: 20px;
      font-size: 20px;
      line-height: 140%;
      font-family: $mainFontBold;
      font-style: normal;
      font-weight: 700;

      /* or 42px */

      letter-spacing: 0.02em;
      text-transform: uppercase;

      color: rgba(66, 71, 77, 0.5);

      @media (min-width: $xs) {
        font-size: 20px;
        margin-bottom: 30px;
      }
      @media (min-width: $sm) {
        font-size: 30px;
        width: 100%;
      }
      @media (min-width: $xl) {
        margin-bottom: auto;
        margin-top: 1rem;
      }

      & span {
        display: block;
        @media (min-width: $sm) {
          display: inline-block;
        }
      }
    }

    & .link-text {
      @media (min-width: 1210px) {
        margin-right: 0;
        margin-bottom: 5%;
      }
      @media (min-width: $lg) {
        margin-right: 15%;
      }
      @media (min-width: 1881px) {
        // margin-left: 0;
        // margin-bottom: auto;
      }
      // @media (min-width: $xl) {
      //   margin-left: 0;
      //   margin-bottom: auto;
      // }
    }
  }
}
