.club-services {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $blue-color;
  padding: 30px;

  margin: 1rem 0 45px;
  border-radius: $borderRadius;

  box-shadow: $boxShadow;

  @media (min-width: 375px) {
    padding: 30px 35px;
  }
  @media (min-width: $xs) {
    padding: 30px;
    padding-bottom: 10px;
    margin-bottom: 30px;
  }
  @media (min-width: $sm) {
    padding: 30px 52px 10px;
    margin: 1rem 0 30px;
    box-shadow: none;
  }

  @media (min-width: 800px) {
    padding: 30px 30px 10px;
  }

  @media (min-width: $md) {
    margin-bottom: 55px;
    padding: 30px 30px 10px;
  }

  @media (min-width: 1024px) {
    padding: 30px 50px 10px;
    padding-left: 80px;
  }
  @media (min-width: 1100px) {
    padding-left: 100px;
  }

  @media (min-width: $xl) {
    padding: 30px 30px 10px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
    gap: 20px;

    justify-content: space-around;

    @media (min-width: $xs) {
      padding-bottom: 20px;
    }

    @media (min-width: 800px) {
      justify-content: center;

    }

    @media (min-width: $md) {
      width: 100%;
      gap: 30px;
    }
    @media (min-width: $xl) {
      width: 100%;
      justify-content: space-around;
    }
  }

