  &__content {
    position: relative;
    padding-left: 35px;
    &::before {
      content: '';
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: 0 0;
    }

    &--1 {
      &::before {
        background-image: url('../img/icons-service/gym.svg');
      }
    }
    &--2 {
      // @media (min-width: $sm) {
      //   order: 1;
      // }
      // @media (min-width: $md) {
