.advantages {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 0.8rem;

  @media (min-width: $sm) {
    margin-bottom: 0;
  }

  &__link-modal {
    height: 100%;
    position: relative;

    background-color: $blue-color;
    border-radius: 22px;
    box-shadow: $boxShadow;

    display: flex;
    flex-direction: column;

    &:focus,
    &:hover {
      background-color: $yellow-color;
    }
  }

  &__more {
    padding: 0 1rem 1rem;

    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    margin-left: auto;
    // text-align: right;
    @media (min-width: $sm) {
      padding: 0 0.5rem 1rem ;
    }
    @media (min-width: $md) {
      padding: 0 1rem 1rem;
    }

    @media (min-width: $xl) {
      padding-right: 2.8rem;
    }

    & .link-text {
      align-items: baseline;
      @media (min-width: $sm) {
        font-size: 14px;
      }
      @media (min-width: $md) {
        // flex-wrap: wrap;
        // align-items: center;
        font-size: 15px;
      }
    }

    & .link-text--more .icon {
      height: 6px;
      margin-left: 0;
      margin-top: 0;

      @media (min-width: $sm) {
        height: 4px;
      }

      @media (min-width: $md) {
        height: 6px;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    height: 100%;

    @include listReset();
    @include linkReset();

    padding: 0.5rem;

    @media (min-width: $sm) {
      padding: 0.65rem 0.7rem;
      height: auto;
      padding-bottom: 0;
    }

    @media (min-width: 992px) and (max-width: 1024px) {
      align-items: center;
    }

    @media (min-width: $lg) {
      height: calc(100% - 49px);
      padding: 12px 17px 0 30px;
    }

    @media (max-width: $md) {
      height: 100%;
    }

    @media (max-width: $xsm) {
      height: auto;
    }

    @media (min-width: $xl) {
      height: auto;
      padding-bottom: 0;
    }

    @media (min-width: $xxl) {
      // padding: 12px 6px 19px 60px;
      padding: 12px 30px 0 50px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    width: 50%;

    color: $primary-color;
    border: 1px solid $white-color;
    padding: 0.6rem 0.5rem 0.5rem 0.9rem;

    @media (min-width: $xs) {
      padding: 0.5rem;
      padding-left: 2rem;
    }

    @media (min-width: $sm) {
      padding: 0 0.2rem;

      width: 100%;
    }

    @media (min-width: 800px) and (max-width: $md) {
      align-items: center;
      justify-content: center;
    }

    @media (min-width: $md) {
      width: 50%;
      height: 50%;
      padding: 0.5rem 0.3rem;
      border-width: 2px;
    }

    @media (min-width: $xl) {
      width: 50%;
      padding: 0.5rem 0.3rem;
      border-width: 2px;
    }
    @media (min-width: $xxl) {
      padding: 0;
    }

    @media (min-width: 1440px) {
      // width: 300px;

      &:nth-child(2n + 1) {
        max-width: 266px;
      }
    }

    & .icon {
      margin-bottom: 0.5rem;

      @media (min-width: $sm) {
        margin-bottom: 0.7rem;
      }
    }

    &--1 {
      border-top-color: transparent;
      border-left-color: transparent;

      @media (min-width: $sm) {
        border-right-color: transparent;
      }

      @media (min-width: $md) {
        border-right-color: $white-color;
      }

      @media (min-width: $xl) {
        border-right-color: $white-color;
        border-bottom-color: $white-color;
        padding-left: 2px;
        padding-bottom: 29px;
      }
      @media (min-width: 1349px) {
        padding-bottom: 8px;
      }
      @media (min-width: $xxl) {
        padding-bottom: 0;
      }

      & .icon {
        width: 33px;
        height: 30px;

        @media (min-width: $sm) {
          width: 49px;
          height: 45px;
          margin-bottom: 1rem;
        }

        @media (min-width: $md) {
          width: 40px;
          height: 37px;
          margin-bottom: 0.5rem;
        }

        @media (min-width: $xl) {
          width: 49px;
          height: 45px;
          margin-bottom: 1rem;
        }
      }
    }
    &--2 {
      border-top-color: transparent;
      border-right-color: transparent;
      padding-left: 1.1rem;

      @media (min-width: $xs) {
        padding-left: 2rem;
      }
      @media (min-width: $sm) {
        border-top-color: $white-color;
        border-left-color: transparent;
        padding-left: 0;
      }

      @media (min-width: $md) {
        border-top-color: transparent;
        border-left-color: $white-color;
        padding-left: 15px;
        padding-bottom: 20px;
        padding-right: 0;
      }

      @media (min-width: $xl) {
        border-top-color: transparent;
        border-left-color: $white-color;
        padding-left: 20px;
        padding-bottom: 8px;
      }

      @media (min-width: 1319px) and (max-width: 1349px) {
        padding-bottom: 29px;
      }

      @media (min-width: 1400px) {
        padding-bottom: 0;
      }
      @media (min-width: 1440px) {
        padding-left: 70px;
      }

      & .icon {
        width: 38px;
        height: 20px;

        @media (min-width: $sm) {
          width: 53px;
          height: 29px;
          margin-bottom: 25px;
        }

        @media (min-width: $md) {
          width: 48px;
          height: 30px;
          margin-bottom: 0.5rem;
        }

        @media (min-width: $xl) {
          width: 53px;
          height: 29px;
          margin-bottom: 25px;
        }
      }
    }
    &--3 {
      border-bottom-color: transparent;
      border-left-color: transparent;

      @media (min-width: $sm) {
        border-right-color: transparent;
        border-bottom-color: $white-color;
      }

      @media (min-width: $md) {
        border-bottom-color: transparent;
        border-right-color: $white-color;
      }

      @media (min-width: $xl) {
        border-bottom-color: transparent;
        border-right-color: $white-color;
        padding-top: 7px;
      }

      & sub {
        bottom: 5px;

        @media (min-width: $sm) {
          bottom: 10px;
        }
      }

      & .icon {
        width: 28px;
        height: 26px;

        @media (min-width: $sm) {
          width: 55px;
          height: 55px;
          margin-left: -10px;
        }

        @media (min-width: $md) {
          width: 38px;
          height: 36px;
          margin-bottom: 0.5rem;
        }

        @media (min-width: $xl) {
          width: 55px;
          height: 55px;
          margin-left: -10px;
        }
      }
    }
    &--4 {
      border-right-color: transparent;
      border-bottom-color: transparent;
      padding-left: 1.1rem;

      @media (min-width: $xs) {
        padding-left: 2rem;
      }

      @media (min-width: $sm) {
        border-left-color: transparent;
        padding-left: 0;
      }

      @media (min-width: $md) {
        border-left-color: $white-color;
        padding-left: 15px;
      }

      @media (min-width: $xl) {
        border-left-color: $white-color;
        padding-left: 20px;
        padding-top: 7px;
      }

      @media (min-width: 1440px) {
        padding-left: 71px;
      }

      & .icon {
        width: 33px;
        height: 25px;

        @media (min-width: $sm) {
          width: 49px;
          height: 36px;
          margin-bottom: 22px;
          margin-left: 2px;
        }

        @media (min-width: $md) {
          width: 40px;
          height: 32px;
          margin-bottom: 0.5rem;
        }

        @media (min-width: $xl) {
          width: 49px;
          height: 36px;
          margin-bottom: 22px;
          margin-left: 2px;
        }
      }
    }
  }

  & h1 {
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-size: 27px;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    text-align: center;

    @include ff(
      'font-size',
      (
        375px: 27px,
        425px: 36px,
        1024px: 42px,
        1199px: 60px,
        1200px: 40px,
        1230px: 40px,
        1250px: 44px,
        1275px: 46px,
        1290px: 46px,
        1440px: 60px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 0;
    }

    color: rgba(73, 76, 79, 0.5);

    @media (max-width: 1023px) {
      display: none;
    }
    @media (max-width: $xs) {
      display: block;
    }
    @media (min-width: $sm) {
      margin-top: 0;
      text-align: left;
    }

    @media (min-width: $lg) {
      display: block;
    }
  }

  &__title {
    position: relative;
    color: $primary-color;
    font-family: $mainFontBold;
    font-size: 13px;
    font-weight: 700;
    line-height: 140%;

    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 10px,
        375px: 13px,
        1024px: 13px,
        1200px: 12px,
        1300px: 14px,
        1440px: 15px,
      )
    );

    @media (min-width: $sm) {
      margin-top: -10px;
    }

    @media (min-width: $lg) {
      // font-size: 15px;
      bottom: 13px;
      left: 3px;
    }

    @media (min-width: $lg) {
      margin-top: 0;
    }

    & span {
      display: block;

      @media (min-width: $sm) {
        display: inline-block;
        padding-left: 0.2rem;
      }
    }
  }

  &__count {
    display: flex;
    align-items: flex-end;
  }

  &__count span {
    margin-right: 0.5rem;
  }
}
