.timer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  @media (min-width: $sm) {
    justify-content: space-between;
  }
  @media (min-width: $md) {
    justify-content: center;
  }

  @media (min-width: $xl) {
    justify-content: space-between;
    padding: 1.6rem 8rem 1.5rem;
  }
  @media (min-width: $xxl) {
    // padding: 1.6rem 9rem 1.5rem;
    padding: 0 9rem;
  }

  & .title {
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 36px;
    line-height: 130%;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        375px: 17px,
        768px: 26px,
        1280px: 36px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 0;
      text-align: right;
      width: 35%;
    }
    @media (min-width: $md) {
      width: auto;
      padding-right: 1rem;
    }

    @media (min-width: $xl) {
      width: 44%;
    }
    @media (min-width: $xxl) {
      width: 40%;
    }

    & span {
      @media (min-width: $md) {
        display: block;
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    width: 100%;

    @media (min-width: $sm) {
      justify-content: flex-end;
      width: 65%;
    }
    @media (min-width: $md) {
      justify-content: space-between;
      width: auto;
    }
    @media (min-width: $xl) {
      width: 56%;
    }
    @media (min-width: $xxl) {
      max-width: 600px;
      width: 60%;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 25vw;
    height: 25vw;

    // @media (min-width: $sm) {
    //   width: 17vw;
    //   height: 17vw;
    // }

    @media (min-width: $sm) {
      width: 150px;
      height: 150px;
    }
    @media (min-width: $xl) {
      width: 143px;
      height: 143px;
    }
    @media (min-width: $lg) {
      width: 160px;
      height: 160px;
    }
    @media (min-width: $xxl) {
      width: 180px;
      height: 180px;
    }

    & svg {
      position: relative;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(270deg);
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke-width: 10;
      stroke: $blue-color;

      & .circle {
        // stroke: var(--clr);
        // stroke: $blue-color;
        stroke-dasharray: 440;
        stroke-linecap: round;
      }
    }

    & .dots {
      z-index: 100;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      // &::before {
      //   content: '';
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   width: 15px;
      //   height: 15px;
      //   background-color: $blue-color;
      //   box-shadow: $boxShadow;
      // }
    }
  }

  &__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-family: $mainFontBold;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    line-height: normal;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        375px: 21px,
        768px: 38px,
        1280px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          375px: 11px,
          768px: 17px,
          1024px: 17px,
          1440px: 24px,
        )
      );
    }
  }
}
