.sidebar-clubs {
  right: -650px;
  transition: opacity 0.3s linear, right 0.3s ease-out;
  overflow: auto;

  &.show {
    right: 0;
    min-height: 560px;
  }

  &__dialog {
    height: 100%;
  }

  & .btn-close {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border-radius: 50%;

    & .icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 30px;
      height: 30px;
      stroke: $white-color;
      stroke-width: 2px;
    }
  }

  &__content {
    max-height: 100%;
    background-color: transparent;
  }

  &__body {
    padding: 0.3rem 0;
    background-color: transparent;
  }
}
