.page-club {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;

  &__header {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    & > .title {
      display: none;
      margin-bottom: 15px;

      font-size: 50px;

      letter-spacing: 0.02em;
      text-transform: uppercase;

      @media (min-width: $md) {
        display: inline-block;
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;

    @media (min-width: $xs) {
      margin-bottom: 35px;
    }
  }

  &__header-item {
    width: 100%;
    margin-bottom: 1rem;

    &:first-child {
      flex-grow: 1;
      // @media (min-width: $md) {
      //   width: 40%;
      // }
    }

    @media (min-width: $xs) {
      width: 35%;
      margin-bottom: 0;
    }
    @media (min-width: 800px) {
      width: 25%;
    }
    @media (min-width: $md) {
      width: 45%;
    }
    @media (min-width: $xl) {
      width: 41%;
    }
    @media (min-width: $xxl) {
      width: 33%;
    }

    &.working-hours {
      @media (min-width: 800px) {
        width: 30%;
      }
      @media (min-width: $md) {
        width: 460px;
      }
    }

    & .title {
      margin: 0;
      font-size: 27px;
      text-transform: uppercase;
      letter-spacing: 0.02em;

      @media (min-width: $sm) {
        font-size: 40px;
        letter-spacing: 0.02em;
      }
      @media (min-width: $md) {
        font-size: 20px;
        display: none;
      }

      & span {
        display: block;
        @media (min-width: $md) {
          display: inline-block;
        }
      }
    }

    & .raiting {
      margin-bottom: 0.5rem;
      @media (min-width: $md) {
        margin-bottom: 0;
      }
    }
  }

  &__header-map {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    font-weight: 400;
    font-size: 20px;
    line-height: 130%;

    & .link-text {
      margin-top: 0.5rem;
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      text-decoration: none;
      color: #b3b3b3;
      text-transform: initial;

      &:focus,
      &:hover {
        &.link-text--more {
          color: $primary-color;
        }
        &.link-text--more .icon {
          width: 40px;
          transform: translateX(6px);
          transition: $transition-base;
        }
      }
    }
  }

  &__promo {
    &.container {
      overflow: hidden;
      @media (max-width: 767px) {
        padding: 0;
      }
    }

    &-inner {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      background-color: $blue-color;

      margin-bottom: 30px;

      box-shadow: $boxShadow;

      width: 100%;

      @media (min-width: 769px) {
        margin-bottom: 2rem;
        border-radius: $borderRadius22;
        box-shadow: none;
      }
      @media (min-width: 1100px) {
        flex-direction: row;
        justify-content: space-between;
      }
    }
  }

  & .slider {
    width: 100%;
    @media (max-width: 767px) {
      border-radius: 0;
    }
    @media (min-width: 800px) {
      min-height: 435px;
    }
    @media (min-width: 1100px) {
      width: 65%;
      border-radius: $borderRadius22;
    }
    @media (min-width: $xl) {
      width: 70%;
    }
  }

  & .swiper-wrapper {
    @media (max-width: 767px) {
      max-height: 400px;
    }
  }

  & .form-book {
    width: 100%;
    @media (min-width: 1100px) {
      width: 35%;
    }
    @media (min-width: $xl) {
      width: 30%;
    }
  }

  &__record {

    &.container {
      @media (max-width: 767px) {
        // padding: 0;
      }
    }

    &-inner {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      // margin: 3rem 0;

      width: 100%;

      border-radius: 10px;
      // box-shadow: $boxShadow;
      margin-bottom: 45px;

      @media (min-width: 1025px) {
        flex-direction: row;
        justify-content: space-between;
        border-radius: $borderRadius;
        box-shadow: none;
      }
    }

    & .slider-content {
      width: 100%;
      height: 400px;
      margin-bottom: 15px;
      border-radius: 10px;
      overflow: hidden;

      @media (max-width: $sm) {
        box-shadow: none;
      }
      @media (min-width: $sm) {
        height: 435px;
        margin-bottom: 45px;
        border-radius: $borderRadius22;
      }
      @media (min-width: 1024px) {
      }
      @media (min-width: $xl) {
        max-width: 730px;
        margin-bottom: 0;
      }
    }

    & .record {
      width: 100%;
      margin: 0;
      margin-top: 2rem;
      @media (max-width: 1024px) {
        width: 100%;
      }
      @media (min-width: $sm) {
        margin-top: 0;
      }
      @media (min-width: $xl) {
        width: calc(100% - 730px - 40px);
      }
    }

    & .subscription {
      margin-top: 30px;
      margin-bottom: 15px;
      @media (min-width: $sm) {
        margin-top: 0;
        margin-bottom: 30px;
      }
      @media (min-width: $xl) {
        order: 1;
        margin-top: 50px;
      }
    }
  }
}
