.clubs-info {
  position: relative;
  @include listReset();
  // border-top: 1px solid;
  // border-bottom: 1px solid;
  // margin: 2rem 0;

  display: flex;
  flex-direction: column;

  width: 100%;

  // @media (min-width: $xs) {
  //   margin-right: 90px;
  // }
  @media (min-width: 769px) {
    margin-right: 50px;
  }
  @media (min-width: $md) {
    margin-right: 0;
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $primary-color;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  &__item {
    flex-grow: 1;
    position: relative;
    padding-top: 14px;
    padding-bottom: 14px;
    margin: 0;
    color: $primary-color;
    padding-left: 30px;

    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (min-width: $sm) {
      padding-left: calc(20px + 30px);
    }
    @media (min-width: $xl) {
      padding-top: 16px;
      padding-bottom: 16px;
    }
    @media (min-width: $xxl) {
      padding-top: 24px;
      padding-bottom: 24px;
    }



    &:not(:last-child) {
      border-bottom: 1px solid;
    }

    @media (min-width: $md) {
      padding-left: calc(40px + 30px);

      &::before {
        top: 24px;
        left: 40px;
      }
    }
  }

  &__club {
    position: relative;
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        375px: 15px,
        768px: 20px,
      )
    );

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -25px;
      // transform: translateY(-50%);
      width: 17px;
      height: 30px;
      background-image: url('../img/icons/fh.svg');

      @media (min-width: $sm) {
        top: 0;
        left: -25px;
      }
    }
  }

  &__text {
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        375px: 16px,
        768px: 20px,
      )
    );
  }
}
