.news-wg {
  margin: 35px 0;
  margin-top: 40px;
  display: flex;
  flex-direction: column;

  @media (min-width: $md) {
    margin: 35px 0;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: normal;
    margin-bottom: 10px;

    @media (min-width: $sm) {
      align-items: baseline;
    }

    @media (min-width: $md) {
      align-items: flex-end;
      margin-bottom: 30px;
    }

    & .icon {
      width: 30px;
      height: 30px;

      @media (min-width: $xs) {
        width: 50px;
        height: 48px;
      }
      @media (min-width: $md) {
        width: 66px;
        height: 63px;
      }
    }
  }

  & .title {
    margin: 0;
    margin-left: 11px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 26px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: rgba(66, 71, 77, 0.5);

    @include ff(
      'font-size',
      (
        420px: 26px,
        992px: 30px,
        1440px: 40px,
      )
    );

    @media (min-width: $sm) {
      font-size: 30px;
    }

    @media (min-width: $xs) {
      margin-left: 22px;
    }
    @media (min-width: $md) {
      margin-left: 25px;
      line-height: none;
    }
  }

  & .link-text {
    margin-left: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    font-family: $mainFont;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;
    transition: $transition-base;

    & .icon {
      width: 30px;
      // height: 5px;
      margin-left: 5px;
      transition: $transition-base;
    }

    &:focus,
    &:hover {
      text-decoration: none;
      & .icon {
        transform: translateX(6px);
        // transform: translateX(6px) rotate(-90deg) scale(1.2);
        transition: $transition-base;
      }
    }
  }

  &__list {
    @include listReset();
    margin: 10px 0;

    @media (min-width: $xs) {
      padding-left: 80px;
    }
    @media (min-width: $md) {
      margin: 30px 0;
    }
    @media (min-width: $xl) {
      padding-left: 100px;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $xs) {
      margin-bottom: 35px;
    }

    &-date {
      padding: 16px 25px;
      margin-bottom: 10px;

      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;

      background-color: $blue-color;
      box-shadow: $boxShadow;
      border-radius: $borderRadius;
    }

    &-text {
      width: 100%;
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;

      @media (min-width: $xs) {
        width: calc(100% - 160px - 22px);
        margin-left: 22px;
      }
      @media (min-width: $sm) {
        font-size: 20px;
        line-height: 130%;
      }

      & p {
        margin-bottom: 0.2rem;
      }
    }
  }
}
