.hero {
  font-family: $mainFontBold;
  font-weight: 700;
  line-height: 130%;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__clubs-info {
    display: flex;

  }

  &__clubs-info,
  &__pic {
    width: 100%;
    margin-bottom: 35px;

    @media (min-width: $md) {
      width: 50%;
      margin: 2rem 0;
    }
  }

  &__pic {
    display: flex;
    justify-content: flex-end;

    @media (min-width: $md) {
      padding: 0 0 0 3rem;
    }

    & img {
      width: 324px;
      width: 100%;
      min-height: 217px;
      max-width: 100%;
      height: 100%;
      border-radius: $borderRadius;

      @media (min-width: $sm) {
        width: 100%;
        min-height: 269px;
      }

      @media (min-width: $md) {
        width: 100%;
        // min-height: 305px;
        margin-left: auto;
        max-height: 430px;
      }

      @media (max-width: $md) {
        width: 100%;
      }
    }

    & picture {
      width: 100%;
    }
  }

  & h1 {
    margin: 0;
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;
    font-size: 60px;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: rgba(73, 76, 79, 0.5);

    @include ff(
      'font-size',
      (
        375px: 20px,
        768px: 50px,
        1440px: 60px,
      )
    );

    @media (min-width: $md) {
      margin-bottom: 1rem;
    }

    & span {
      display: block;

      @media (min-width: $md) {
        display: inline-block;
      }
    }
  }
}
