.form-message-modal {

  padding: 1rem 0;


  @media (min-width: $xs) {
    padding: 3rem 2rem;
  }
  @media (min-width: $sm) {
    padding: 1rem;
  }

  &--bg {
    @media (min-width: $sm) {
      background-image: url(../img/forms/1.jpg);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: 45% 100%;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

  }

  &__title {
    max-width: 280px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: $primary-color;
    text-align: center;

    @media (min-width: $md) {
      text-align: left;
    }

    & span {
      display: block;
    }
  }
}
