.form-clubs {
  background-color: $gray-light-color;

  padding: 40px 0;
  box-shadow: $boxShadow;
  border-radius: 10px;
  overflow: hidden;

  @media (min-width: $xs) {
    border-radius: $borderRadius22;
  }

  @media (min-width: 769px) {
    border-radius: 0;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
  }

  &__title {
    margin-bottom: 1.3rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: #ffffff;

    @include ff(
      'font-size',
      (
        320px: 20px,
        768px: 40px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 2rem;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    width: 100%;
  }

  & .form-label {
    padding-bottom: 0;
    margin-bottom: 1rem;
    color: #ffffff;
    width: 100%;

    @media (min-width: $xs) {
      width: calc(100% / 2 - 1rem);
      margin-right: 1rem;
    }

    @media (min-width: $md) {
      width: calc(23% - 1rem);
      margin-right: 1rem;
    }
    @media (min-width: $xl) {
      width: calc(23% - 1rem);
      margin-right: 1rem;
    }
    @media (min-width: $xxl) {
      width: calc(100% / 4 - 1rem);
      margin-right: 1rem;
    }
  }

  & .form-select {
    &:focus,
    &:hover {
      box-shadow: 0 0 0 0.15rem rgba(44, 58, 69, 0.305);
    }
    @media (min-width: $xl) {
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 1rem;
    @media (min-width: $xs) {
      flex-direction: row;
      width: auto;
      margin-top: 0;
    }
    @media (min-width: $md) {
      margin-left: auto;
    }
  }

  & .button {
    width: 100%;
    margin-bottom: 1rem;

    &:not(:last-child) {
      margin-right: 0.5rem;

      @media (min-width: $xxl) {
        margin-right: 1rem;
      }
    }

    @media (min-width: $xs) {
      width: auto;
    }
  }
}
