.club-preview {
  max-width: 645px;
  width: 100%;
  padding: 15px 0;

  display: flex;
  flex-wrap: wrap;

  @media (min-width: $sm) {
    right: 20px;
  }

  &__header {
    padding: 15px;
    width: 100%;

    @media (min-width: $xs) {
      padding: 25px 35px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__body {
    order: 1;
    @media (min-width: $sm) {
      order: 0;
    }
  }

  &__text {
    padding: 25px 20px 15px 40px;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;

    @media (max-width: $xs) {
      display: none;
    }
  }

  &__contacts {
    width: 95%;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $md) {
      max-width: 70%;
      width: 50%;
      flex-grow: 1;
    }
  }

  & .title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    text-transform: uppercase;
  }

  &__working {
    display: flex;
    padding-top: 10px;

    @media (min-width: $xs) {
      display: none;
      padding-top: 0;
    }

    @media (min-width: $md) {
      display: flex;
      max-width: 50%;
    }
  }

  &__footer {
    padding: 0 15px 10px;

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;

    @media (min-width: $xs) {
      padding: 10px 40px 25px;
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
    }

    @media (min-width: $md) {
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
    }

    & .link-text {
      text-transform: initial;
      padding: 0.5rem 0;
      text-decoration: none;
      @media (max-width: $xs) {
        margin-left: 0;
        margin-right: auto;
        margin-bottom: 0.5rem;
        padding: 0.5rem;
        width: auto;
        border: 1px solid;
        border-radius: $borderRadius;
        border-color: $primary-color;
        &:first-child {
          flex-grow: 1;
          background-color: $red-color;
          color: $white-color;
          border-color: $red-color;
        }
      }

      @media (min-width: $xs) {
        max-width: 50%;
        &:first-child {
          max-width: 60%;
          font-family: $mainFontBold;
          font-weight: 700;
        }
      }

      @media (min-width: 769px) {
        max-width: 100%;
      }

      & .icon {
        margin-left: 0.3rem;
        height: 5px;
      }
    }
  }

  &--result {
    padding: 0;
    max-width: 670px;
    width: 100%;
    border-radius: $borderRadius22;

    overflow: hidden;

    @media (min-width: $xs) {
      max-width: 443px;
    }
    @media (min-width: $md) {
      max-width: 670px;
    }

    & .club-preview__header {
      padding: 15px;

      @media (min-width: $xs) {
        padding: 10px 15px 10px 25px;
      }

      & .title {
        @media (max-width: $sm) {
          font-size: 17px;
          padding-bottom: 0.5rem;
        }

        @media (min-width: $md) {
          font-size: 19px;
        }
      }

      & .raiting {
        display: none;


        @media (min-width: $xs) {
          display: block;
        }
      }

      & .working-hours {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
      }
    }

    & .club-preview__info {
      width: 100%;
      @media (min-width: $xs) {
        width: 90%;
      }

      // @media (min-width: 769px) {
      //   width: 50%;
      // }
      @media (min-width: $md) {
        width: 65%;
      }
    }

    & .club-preview__contacts {
      max-width: 100%;
      width: 100%;
    }

    & .club-preview__links {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: flex-start;
      padding: 1rem 0;
      @media (min-width: $xs) {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
      }

      @media (max-width: $sm) {
        padding: 0;
      }

      @media (min-width: $md) {
        width: 50%;
        flex-direction: column-reverse;
        justify-content: flex-end;
        align-items: flex-end;
        padding: 2rem 0;

      }
      @media (min-width: $md) {
        width: 35%;

      }

      & .link-text {
        text-transform: initial;
        padding: 0.5rem 0;
        @media (max-width: $xs) {
          margin-left: 0;
          margin-right: auto;
          margin-bottom: 0.5rem;
          padding: 0.5rem;
          width: auto;
          border: 1px solid;
          border-radius: $borderRadius;
          border-color: $primary-color;
          &:first-child {

            background-color: $red-color;
            color: $white-color;
            border-color: $red-color;
          }
        }

        @media (min-width: $xs) {
          max-width: 50%;
          &:first-child {
            max-width: 60%;
            flex-grow: 1;
            font-family: $mainFontBold;
            font-weight: 700;
          }
        }

        @media (min-width: 769px) {
          max-width: 100%;
          padding: 0;

          &:first-child {
            max-width: 100%;
          }
        }

        & .icon {
          margin-left: 0.3rem;
          height: 5px;
        }
      }
    }

    & .club-preview__body {
      width: 100%;
    }

    & .club-preview__pic {
      display: none;

      @media (min-width: $xs) {
        display: block;
        height: 120px;
      }

      & img {
        height: 100%;
      }
    }

    & .club-preview__working {
      padding-top: 0.5rem;
      width: 100%;
      max-width: 100%;

      @media (min-width: $xs) {
        display: none;
      }
      @media (min-width: $md) {
        display: flex;
      }
      & .working-hours__club {
        @media (min-width: $xs) {
          flex-direction: row;

          &::before {
            top: 0;
          }
        }

        & span {
          &:not(:last-child) {
            margin-right: 0.25rem;
          }
        }
      }
    }
  }

  &--slide {
    & .club-preview__header {
      padding: 15px;
    }
    & .club-preview__contacts {
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__address {
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__working {
      padding-top: 1rem;
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__pic {
      width: 100%;
      min-height: 175px;
      overflow: hidden;

      & img {
        height: 100%;
        min-height: 175px;
      }
    }

    & .club-preview__footer {
      padding: 15px;
    }

    & .club-preview__footer .link-text:first-child {
      max-width: 100%;
      order: 1;
    }
  }
  &--list {
    & .club-preview__header {
      padding: 15px;
    }
    & .club-preview__contacts {
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__address {
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__working {
      padding-top: 1rem;
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__pic {
      width: 100%;
      min-height: 175px;
      overflow: hidden;

      & img {
        height: 100%;
        min-height: 175px;
      }
    }

    & .club-preview__footer {
      padding: 15px;
      flex-direction: column-reverse;
      order: 1;
      width: 100%;
      max-width: 100%;
    }

    & .club-preview__footer .link-text{
      max-width: 100%;
    }
  }
}
