:root {
  --s-mb: 30px;
  --s-mr: 0.25rem;
  --s-count: 2;
  --s-w-size: calc(100% / var(--s-count) - var(--s-mr));

  @media (min-width: $sm) {
  }
  @media (min-width: $md) {
    --s-mb: 50px;
    --s-mr: 1rem;
    --s-count: 4;
  }
  @media (min-width: $lg) {
    --s-count: 4;
  }
}

.page-sale {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  &__slider {
    width: 100%;

    margin-bottom: 40px;
    @media (min-width: $sm) {
      min-height: 300px;
      margin-bottom: var(--s-mb);
    }

    & .swiper-slide {
      @media (min-width: $sm) {
        min-height: 300px;
      }
      @media (min-width: $xl) {
        min-height: 500px;
      }

      & img {
        @media (min-width: $sm) {
          min-height: 300px;
        }
        @media (min-width: $xl) {
          min-height: 500px;
        }
      }
    }
  }
  &__form {
    width: 100%;

    margin-bottom: var(--s-mb);
  }
  &__list {
    width: 100%;

    margin-bottom: var(--s-mb);

    @media (min-width: $md) {
      width: 50%;
    }
  }
  &__box-kids {
    width: var(--s-w-size);

    margin-bottom: var(--s-mb);

    & .section {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
    }
  }
  &__box-korus {
    width: var(--s-w-size);
    margin-bottom: var(--s-mb);

    & .section {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
    }
  }
  &__content {
    width: 100%;
  }

  &__timer {
    width: 100%;
    margin-bottom: var(--s-mb);
  }
}
