:root {
  --size-h: 500px;
  @media (min-width: $md) {
    --size-h: 650px;
  }
  @media (min-width: $xl) {
    --size-h: 850px;
  }
}
.clubs-result {
  display: none;
  // @media (min-width: 450px) {
  //   top: -10px;
  //   left: auto;
  // }
  @media (min-width: $sm) {
    display: block;
    position: absolute;
    z-index: 10;
    top: -30px;
    right: 0;
    left: auto;
    max-width: 450px;
    // height: 100%;

    overflow: hidden;

    height: var(--size-h);
    max-height: var(--size-h);
    border-radius: $borderRadius22;

    padding-top: 1rem;
  }

  @media (min-width: $md) {
    max-width: 650px;
  }

  &__item {
    background-color: #f6f6f6;
    background-color: $blue-color;
    border-radius: $borderRadius22;
    box-shadow: -1px 4px 24px rgba(0, 0, 0, 0.2);

    &:nth-child(2n) {
      background-color: $yellow-color;
    }

    @media (min-width: $xs) {
      background-color: #f6f6f6;

      &:nth-child(2n) {
        background-color: #f6f6f6;
      }
    }

    & .club-preview__address {
      font-size: 17px;
    }

    & .club-preview__links {
      @media (max-width: $sm) {
        padding: 0;
      }
    }
  }

  & .my-custom-scrollbar {
    height: 100%;
    min-height: var(--size-h);
    scrollbar-gutter: stable;

    &::-webkit-scrollbar {
      width: 10px;
    }

    &::-webkit-scrollbar-track {
      background-color: $blue-color;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $primary-color;
      // box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.7);
    }
  }

  & .btn-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 11;
    color: $primary-color;
    opacity: 1;
    background: none;
    width: 30px;
    height: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;

    outline: none;
    cursor: pointer;

    &:focus,
    &:hover {
      outline: none;
      box-shadow: none;
    }

    & .icon {
      width: 90%;
      height: 80%;
      margin: 2px;
      fill: $primary-color;
      stroke: $yellow-color;
    }
  }
}
