.working-hours {
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $xs) {
    column-count: 2;
    flex-wrap: nowrap;
    flex-direction: column;
  }
  @media (min-width: $md) {
    flex-direction: row;
    min-width: 400px;
    width: 460px;
    max-width: 100%;
  }
  @media (min-width: $lg) {
    min-width: 450px;
  }

  & .subtitle {
    margin: 0;
    margin-bottom: 14px;

    font-size: 14px;
    line-height: 140%;

    text-transform: initial;
    letter-spacing: 0;

    @media (min-width: $sm) {
      font-size: 20px;
      line-height: 130%;
    }

    & span {
      // display: inline-flex;

      @media (min-width: $sm) {
        display: flex;
      }
    }
  }

  &__item {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
      margin-bottom: 1rem;
      @media (min-width: $xs) {
        padding-bottom: 1rem;
        margin-bottom: 1rem;
      }

      @media (min-width: $md) {
        margin-bottom: 0;
        padding-bottom: 0;
        padding-right: 25px;
      }
    }

    &:last-child {
      min-width: 200px;
      padding-right: 0;

      @media (min-width: $sm) {
        min-width: 240px;
      }
    }

    @media (min-width: $md) {
      width: 50%;
      min-width: 200px;

      &:last-child {
        min-width: 260px;
        padding-left: 35px;
      }
    }

    @media (min-width: $xs) {
      &:not(:last-child) {
        &::before {
          content: '';
          position: absolute;
          z-index: 1;

          background-color: $primary-color;

          @media (max-width: 991px) {
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
          }

          @media (min-width: $md) {
            top: 50%;
            right: 0;
            width: 1px;
            height: 100%;
            transform: translateY(-50%);
          }
        }
      }
    }
  }

  &__club-label span {
    display: inline-block;
    padding-left: 0.5rem;
  }

  &__club,
  &__managers {
    position: relative;
    display: flex;
    flex-direction: column;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    padding-left: 30px;

    &::before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 3px;
      left: 0;
      width: 20px;
      height: 20px;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: contain;
    }
  }

  &__club {
    &::before {
      background-image: url('../img/icons/hours.svg');
    }
  }
  &__managers {
    &::before {
      background-image: url('../img/icons/man.svg');
    }
  }
}
