.modal-tour {
  & .modal-content {
    padding: 1rem 0;

    @media (min-width: $sm) {
      padding: 0;

    }
  }

  & .modal-dialog {
    @media (min-width: $sm) {
      max-width: 700px;
    }
  }

  & .modal-title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $sm) {
      width: 50%;
    }
  }

  & .modal-header {
    border: none;
    font-family: $mainFont;
    font-weight: 700;

    @media (min-width: $sm) {
      padding-left: 57px;
      padding-top: 50px;
    }
  }

  // & .btn-close {
