  //   opacity: 1;
  //   color: $white-color;
  //   background: none;

  //   display: flex;
  //   overflow: hidden;
  //   align-items: center;
  //   justify-content: center;
  //   width: 30px;
  //   height: 30px;

  //   @media (min-width: $sm) {
  //     position: absolute;
  //     top: 2rem;
  //     right: 2rem;
  //   }
  // }

  & .modal-body {
    display: flex;
    flex-direction: column;

    @media (min-width: $sm) {
      padding-left: 57px;
      padding-bottom: 50px;
      width: 50%;
    }
  }
}
