.modal-kids {
  & .modal-body {
    padding: 1.5rem;
    @media (min-width: $xs) {
      padding: 2rem 2rem 3rem;
    }
    @media (min-width: $md) {
      padding: 1rem 0 1rem 1rem;
    }
    @media (min-width: $xxl) {
      padding: 1rem 1rem 2rem 2rem;
    }

    & .list {
      padding-left: 0;

      & li {
        position: relative;
        padding-left: 1rem;
        line-height: normal;

        &::before {
          content: '';
          position: absolute;
          top: 60%;
          left: 0;
          transform: translateY(-50%);
          width: 5px;
          height: 5px;
          background-color: $primary-color;
          border-radius: 50%;
        }
      }
    }

    & .form-offers__btns {
      padding-top: 0;
      padding-right: 0;
      width: 100%;
    }

    & .button--red {
      font-family: $mainFontBold;
      padding: 16px 25px;
    }
  }
}
