.gallery-clubs-preview {
  position: relative;
  overflow: hidden;
  border-radius: 0;
  box-shadow: none;
  padding: 15px 10px;
  margin-bottom: 2rem;

  @media (min-width: 992px) {
    padding: 15px;
  }


  & .swiper-wrapper {
    padding-right: 15px;
    padding-bottom: 20px;
    @media (min-width: 1024px) {
      display: flex;
      // flex-wrap: wrap;
      justify-content: space-between;

      box-sizing: border-box;
    }
  }

  & .swiper-slide {
    @media (min-width: 1024px) {
      width: 23%;
    }
  }

  & .swiper-slide:before {
    background: none;
  }

  & .swiper-pagination {
    bottom: -10px;
  }

  & .swiper-pagination-bullet {
    background-color: $primary-color;
    border-color: $primary-color;

    &-active {
      background-color: transparent;
    }
  }

  & .club-preview {
    // background-color: #f6f6f6;
    border-radius: $borderRadius;
    box-shadow: $boxShadowdark;
    overflow: hidden;

    @media (min-width: 1024px) {
      box-shadow: $boxShadowdark;
    }
  }
}
