.modal-history {
  & .modal-dialog {
    @media (min-width: $sm) {
      max-width: 690px;
    }
    @media (min-width: $md) {
      max-width: 856px;
    }
  }
  & .modal-body {
    @media (min-width: $sm) {
      padding: 32px;
    }
  }

  & .modal-content {
    border-radius: 22px;
    padding-bottom: 157px;
    background-image: url('../img/modal/girl-swimming-pool-mob.jpg');
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-size: 100% 157px;

    @media (min-width: $sm) {
      background-image: var(--pic);
      padding-bottom: 220px;
      background-size: 100% 220px;
    }
    & .btn-close {
      // z-index: 2;
      // position: absolute;
      // top: 1rem;
      // right: 1rem;
      // cursor: pointer;
      // width: 0.6rem;
      // height: 0.6rem;
      // border-radius: 50%;
      color: $primary-color;
      // opacity: 1;
      // outline: none;

      // &:focus,
      // &:hover {
      //   outline: none;
      // }

      // @media (min-width: $sm) {
      //   top: 30px;
      //   right: 32px;
      // }

      & .icon {
        fill: $primary-color;
        stroke: $white-color;

      }
    }
  }
}
