:root {
  --box-pd-base: 37px;
  --box-pd: 37px 1rem;

  @media (min-width: $lg) {
    --box-pd: 0 30px 37px;
  }
  @media (min-width: $xl) {
    --box-pd: 0 55px 30px 37px;
  }
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  border-radius: $borderRadius;
  box-shadow: $boxShadow;
  background-color: $blue-color;

  width: 100%;

  text-decoration: none;
  color: $primary-color;

  &:focus,
  &:hover {
    color: $primary-color;
    text-decoration: none;
  }

  @media (min-width: $xs) {
    // width: 40vw;
  }
  @media (min-width: $md) {
    // padding: 3.4rem 2.7rem 1rem 2rem;
  }
  @media (min-width: $lg) {
    margin-top: 0;
    width: 50%;

    padding: var(--box-pd);
  }

  @media (min-width: $xl) {
    min-width: 295px;
  }

  &--job {
    align-items: flex-end;
    padding: 26px 33px 29px 33px;
    padding-top: 39px;

    @media (min-width: $sm) and (max-width: 800px) {
      // max-width: 320px;
      width: 47vw !important;
    }

    & .box__title {
      position: relative;
      margin-top: auto;
      margin-bottom: auto;

      margin: auto auto 0;

      padding-top: calc(52px + var(--box-pd-base));

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 94px;
        height: 52px;
        background-image: url('../img/icons/job.svg');
      }
    }
  }

  &--schedule {
    align-items: flex-end;
    padding: 26px 33px 29px 33px;
    padding-top: 39px;
    height: 100%;

    @media (min-width: $sm) and (max-width: 800px) {
      // max-width: 320px;
      width: 47vw !important;
    }

    & .box__title {
      position: relative;
      margin-top: auto;
      margin-bottom: auto;

      margin: auto auto 0;

      padding-top: calc(52px + var(--box-pd-base));

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 58px;
        height: 59px;
        background-image: url('../img/icons/schedule-2.svg');
      }

      & span {
        display: block;
        text-transform: lowercase;
      }
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    width: 100%;
    max-width: 220px;

    margin: 0;
    padding-bottom: 35px;

    & span {
      display: block;
      text-transform: uppercase;
    }
  }

  &__link {
    margin-left: auto;
    margin-right: 0;

    & span {
      margin: 0;
    }
  }

  &:focus,
  &:hover {
    background-color: $yellow-color;

    & .link-text--more .icon {
      width: 40px;
      transform: translateX(6px);
      transition: $transition-base;
    }
  }

  &--yellow {
    background-color: $yellow-color;

    &:focus,
    &:hover {
      background-color: $blue-color;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    padding: 26px 57px 29px 33px;

    @media (min-width: $lg) {
      padding: 0;
    }
  }
}
