.main {
  position: relative;

  &.container {
    @media (max-width: $xs) {
      padding: 0;
    }
  }

  &__grid {
    width: 100%;
    display: flex;
    flex-direction: column;

    // display: grid;
    // display: -ms-grid;

    // // column-gap: 30px;
    // // row-gap: 30px;

    // grid-template-columns: 1fr;

    // grid-template-areas:
    //   's'
    //   'f'
    //   'a'
    //   'h'
    //   'j'
    //   'l'
    //   'd';

    @media (min-width: $xs) {
      display: grid;
      column-gap: 16px;
      row-gap: 16px;
      grid-template-columns: 1fr 1fr;
      -ms-grid-columns: 1fr 1fr;

      grid-template-areas:
        's s'
        'f f'
        'a a'
        'j h'
        'l l'
        'd d';
    }

    @media (min-width: $sm) {
      -ms-grid-columns: 1fr 20px 1fr 20px 1fr 20px 229px;
      grid-template-columns: 1fr 1fr 1fr 229px;

      grid-template-areas:
        's s s a'
        'f f f f'
        'j j h h'
        'l l l l'
        'd d d d';
    }

    @media (min-width: $md) {
      -ms-grid-columns: 1fr 20px 1fr 20px 2fr;
      grid-template-columns: 1fr 1fr 2fr;
      grid-template-areas:
        's s a'
        's s f'
        'j h l'
        'd d d';
    }
    @media (min-width: 1024px) {
      -ms-grid-columns: 1fr 20px 1fr 20px 2fr;
      grid-template-columns: 250px 250px 2fr;
      grid-template-areas:
        's s a'
        's s f'
        'j h l'
        'd d d';
    }

    @media (min-width: $xl) {
      -ms-grid-columns: 1fr 20px 1fr 20px 3fr;
      -ms-grid-rows: 1fr 20px 1fr 20px 1fr 20px 1fr;
      grid-template-columns: 1fr 1fr 3fr;

      grid-template-areas:
        's s a'
        's s f'
        'j h l'
        'd d d';
    }

    @media (min-width: $lg) {
      grid-template-columns: 310px 310px 1fr;
      grid-template-areas:
        's s a'
        's s f'
        'j h l'
        'd d d';
    }

    @media (min-width: 1440px) {
      // margin-top: 14px;
      align-items: flex-start;

      -ms-grid-columns: 1fr 20px 1fr 20px 660px;
      -ms-grid-rows: 1fr 100px 1fr 20px 1fr 20px 1fr;
      grid-template-columns: 1fr 1fr minmax(auto, 660px);

      grid-template-areas:
        's s a'
        's s f'
        'j h l'
        'd d d';
    }

    & > * {
      margin-bottom: 30px;

      @media (min-width: $sm) {
        margin-bottom: 0;
      }
    }
  }

  &__sale {
    grid-area: s;
    -ms-grid-area: s;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $xl) {
      height: 100%;

      -ms-grid-column: 1;
      -ms-grid-column-span: 3;
      -ms-grid-row: 1;
      -ms-grid-row-span: 3;
    }

    & .sale {
      height: 100%;
      width: 100%;

      @media (min-width: $xxl) {
        margin-top: 10px;
      }
    }
  }

  &__adv {
    grid-area: a;
    -ms-grid-area: a;
    display: flex;
    flex-direction: column;
    order: 1;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $sm) {
      order: 0;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 1;
      max-width: 100%;
    }

    & .advantages {
      height: 100%;
      width: 100%;

      @media (min-width: $lg) {
        // height: auto;
      }
    }

    & .advantages__list {
      @media (min-width: $sm) {
        height: 100%;
      }
      @media (min-width: $lg) {
        height: auto;
      }
    }
  }

  &__form {
    grid-area: f;
    -ms-grid-area: f;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 2;
      max-width: 100%;
      height: 100%;
    }

    & .form {
      @media (min-width: $md) {
        min-height: 312px;

        height: 100%;
      }
    }
  }

  &__job {
    grid-area: j;
    -ms-grid-area: j;
    display: flex;
    flex-direction: column;
    order: 2;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }
    @media (min-width: $sm) {
      margin-top: 15px;
      margin-bottom: 15px;
      order: 0;
    }
    @media (min-width: $md) {
      margin-top: 0;
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 1;
      -ms-grid-row: 5;
    }

    & .job {
      height: 100%;
      width: 100%;
      min-height: 295px;
    }
  }

  &__schedule {
    grid-area: h;
    -ms-grid-area: h;
    display: flex;
    flex-direction: column;
    order: 2;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $sm) {
      margin-top: 15px;
      margin-bottom: 15px;
      align-items: flex-end;
      order: 0;
    }
    @media (min-width: $md) {
      margin-top: 0;
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      align-items: start;
      -ms-grid-column: 3;
      -ms-grid-row: 5;
      max-width: 100%;
    }

    & .schedule {
      height: 100%;
      width: 100%;
      min-height: 295px;
    }
  }

  &__slider {
    grid-area: l;
    -ms-grid-area: l;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    order: 2;

    @media (min-width: $sm) {
      order: 0;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 5;
      max-width: 100%;
    }

    & .slider {
      overflow: hidden;
      width: 100%;
      height: 100%;
      @media (max-width: $xs) {
        border-radius: 0;
      }
    }
  }

  &__record {
    grid-area: d;
    -ms-grid-area: d;
    display: flex;
    flex-direction: column;
    order: 2;
    margin-bottom: 0;

    @media (min-width: $sm) {
      order: 0;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 1;
      -ms-grid-column-span: -1;
      -ms-grid-row: 7;
    }

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
}
