.subscription {
  display: flex;
  flex-direction: column;
  margin: 1rem 0 3rem;
  padding: 30px 15px;
  width: 100%;
  background-color: $primary-color;
  border-radius: $borderRadius22;

  @media (min-width: $xs) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px;
  }

  @media (min-width: 900px) {
    padding-top: 60px;
  }
  @media (min-width: $xl) {
    padding: 60px 80px 30px;
    margin: 20px auto 3rem;
    max-width: 1150px;
  }

  &__title {
    width: 100%;
    margin-bottom: 1.6rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $white-color;
    // @media (min-width: 969px) {
    //   max-width: 300px;
    //   width: 100%;
    //   padding-top: 1.3rem;
    //   margin-bottom: 0;
    // }
    // @media (min-width: $md) {
    //   max-width: 365px;
    // }

    & span {
      display: block;
      word-spacing: inherit;
      @media (min-width: $sm) {
        display: inline-block;
      }
    }
  }

  &__form {
    width: 100%;

    // @media (min-width: 601px) {
    //   width: calc(100% - 365px);
    // }

    // @media (max-width: 600px) {
    //   width: 100%;
    // }
  }
}
