.form-and-map {
  margin: 0 0 2rem;
  overflow: hidden;
  position: relative;
  padding-left: 16px;
  padding-right: 16px;

  @media (min-width: $md) {
    padding: 0;
    margin: 2rem 0;
  }

  &__btns {
    display: none;
    z-index: 10;
    position: relative;
    justify-content: center;
    padding-top: 1rem;

    @media (max-width: $xs) {
      display: flex;
    }

    & .button {
      padding: 1rem 0.5rem;
      margin: 0 0.5rem;
      width: 100%;

      flex-grow: 1;

      font-family: $mainFont;
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: $primary-color;

      @media (min-width: 375px) {
        width: auto;
        font-size: 17px;
      }

      @media (min-width: $xs) {
        padding: 1rem;
        font-size: 20px;
      }

      &:focus,
      &:hover {
        border-width: 1px;
        background-color: $yellow-color;
      }

      &:active {
        background-color: $yellow-color;
      }

      &.active {
        background-color: $yellow-color;
      }
    }
  }
}
