.list-services {
  display: flex;
  flex-wrap: wrap;
  padding: 24px 15px;

  background-color: $blue-color;

  @media (min-width: $xs) {
    padding: 24px 30px;
  }
  @media (min-width: $sm) {
    padding: 24px 36px;
  }

  @media (min-width: $md) {
    padding: 1.5rem;
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: $xxl) {
    padding: 24px 17px 24px 33px;
  }

  &__item {
    flex-grow: 1;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    @media (min-width: $xs) {
      width: 50%;
    }
    @media (min-width: $md) {
      margin-bottom: 0;
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 18px;

    @media (min-width: $xs) {
      min-height: 44px;
    }
    @media (min-width: $md) {
      font-size: 15px;
      margin-bottom: 10px;
    }
    @media (min-width: $xl) {
      font-size: 17px;
      margin-bottom: 18px;
    }

    & span {
      display: block;
    }
  }

  &__list,
  &__sublist {
    @include listReset();

    @media (min-width: $xs) {
      padding-right: 1rem;
    }
    @media (min-width: $sm) {
      padding-right: 0.5rem;
    }
    // @media (min-width: $md) {
    //   column-count: 2;
    // }
    // @media (min-width: $lg) {
    //   column-count: 1;
    // }
  }

  &__list-item,
  &__sublist-item {
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: start;
    align-items: flex-start;
    margin-bottom: 5px;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        990px: 15px,
        1440px: 20px,
      )
    );

    @media (min-width: $xl) {
      margin-bottom: 10px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 0.4rem;
    }

    & span {
      width: calc(100% - 30px - 10px);
    }

    & .icon {
      width: 30px;
      height: 18px;
      margin-right: 10px;

      @media (min-width: $sm) {
        height: 18px;
      }
      @media (min-width: $xl) {
        height: 23px;
      }
    }
  }

  &__list-item {
    // &--1 .icon {
    // }
    // &--2 .icon {
    // }
    // &--3 .icon {
    // }
    // &--4 .icon {
    //   width: 33px;
    // }
    // &--5 .icon {
    //   width: 33px;
    // }
    // &--6 .icon {
    //   width: 33px;
    // }
    // &--7 .icon {
    //   width: 33px;
    // }
    // &--8 .icon {
    //   width: 33px;
    // }
    &--9 .icon {
      width: 25px;
      margin-right: 13px;
    }
  }
  &__sublist-item {
    &--1 .icon {
      width: 24px;
      margin-right: 14px;
    }
    // &--2 .icon {
    //   width: 33px;
    // }
    // &--3 .icon {
    //   width: 33px;
    // }
    // &--4 .icon {
    //   width: 33px;
    // }
    &--5 .icon {
      width: 26px;
      margin-right: 12px;
      padding-left: 6px;
    }
    // &--6 .icon {
    //   width: 33px;
    // }
    // &--7 .icon {
    //   width: 33px;
    // }
  }

  // & .icon {
  //   width: 30px;
  //   height: 25px;
  //   margin-right: 10px;
  //   padding-bottom: 4px;
  // }
}
