:root {
  --mr-sl: 1rem;
  @media (min-width: $xs) {
    --mr-sl: 1rem;
  }
  // @media (min-width: $md) {
  //   --mr-sl: 0.5rem;
  // }
}

.form-offers {
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 2rem 1rem;
  background-color: $primary-color;

  border-radius: 22px;

  @media (min-width: $xs) {
    padding: 1.8rem 2rem 3rem;
  }
  @media (min-width: 1024px) {
    padding: 1rem;
  }
  @media (min-width: $xl) {
    // padding: 2rem 1.8rem 1.1rem;
    padding: 1.8rem 1rem;
  }
  @media (min-width: $xxl) {
    // padding: 2rem 1.8rem 1.1rem;
    padding: 1.8rem;
  }

  &__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    align-items: flex-start;

    @media (min-width: $md) {
      justify-content: space-between;
    }

    @media (min-width: $xl) {
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $white-color;
    margin-bottom: 1.6rem;

    @include ff(
      'font-size',
      (
        320px: 17px,
        768px: 17px,
      )
    );

    @media (max-width: 450px) {
      text-transform: uppercase;
    }
    @media (min-width: $sm) {
      margin-bottom: 2.6rem;
    }
    @media (min-width: 1024px) {
      margin-bottom: 1rem;
    }
    @media (min-width: $xl) {
      margin-bottom: 2rem;
    }
  }

  &__btns {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 0;
    padding-top: 1rem;
    width: var(--field-width);

    @media (min-width: $xs) {
      padding-top: 35px;
      padding-right: 0.9rem;
      width: calc(50% - 0.5rem);
    }
    @media (min-width: $sm) {
      padding-right: 1.7rem;
    }
    @media (min-width: $md) {
      padding-right: 0;
      align-items: flex-start;
    }

    @media (min-width: $xl) {
      padding-right: 0.7rem;
    }
  }

  & .form-label {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: var(--field-width);
    flex-grow: 1;

    @media (min-width: $xs) {
      width: calc(50% - 2.5rem);
      margin-right: 0.9rem;
    }
    @media (min-width: $md) {
      width: calc(50% - 0.5rem);

      margin-right: 0.5rem;
    }

    @media (min-width: $xxl) {
      width: calc(50% - 1rem);
      margin-right: 1rem;
    }
  }

  & .form__label-name {
    color: $white-color;

    @media (min-width: $sm) {
      margin-bottom: 6px;
    }
  }

  & .form-control {
    align-items: flex-start;

    &:focus,
    &:hover {
      box-shadow: 0 0 0 0.15rem rgb(226 240 251 / 22%);
    }
  }

  & .form-select {
    &:focus,
    &:hover {
      box-shadow: 0 0 0 0.15rem rgb(226 240 251 / 22%);
    }
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  & button[type='submit'] {
    margin-top: 0;
    padding: 13px 29px;
    max-width: 100%;
    text-align: center;
    @media (min-width: $xl) {
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
    }
    @media (min-width: $xxl) {
      margin-right: auto;
      margin-left: 0;

      padding: 0.8rem 4.2rem;
    }
  }

  &--sale {
    & .form-offers__title {
      font-size: 20px;
    }
    & .form-label {
      @media (min-width: $xs) {
        width: calc(50% - var(--mr-sl));
        margin-right: var(--mr-sl);
      }
      @media (min-width: $md) {
        width: calc(20% - var(--mr-sl));
        margin-right: var(--mr-sl);
      }
      @media (min-width: $xl) {
        width: calc(20% - var(--mr-sl));
        margin-right: var(--mr-sl);
      }
    }

    & .form-offers__btns {
      flex-grow: 1;
      @media (min-width: $xs) {
        width: calc(50% - var(--mr-sl));
        margin-right: var(--mr-sl);
        padding-right: 0;
      }
      @media (min-width: $md) {
        width: calc(27% - var(--mr-sl));
        margin-right: 0;
        align-items: flex-end;
      }
      @media (min-width: $xl) {
        width: calc(30% - var(--mr-sl));
      }
    }

    & .form__check-text {
      font-size: 13px;
      @media (min-width: $md) {
        font-size: 14px;
      }
      @media (min-width: $xl) {
        font-size: 14px;
      }
    }

    & button[type='submit'] {
      margin-left: auto;
      margin-right: 0;
    }
  }
}
